.header-light
{
  background-color: rgba(250, 250, 250, 0.8);
}
.header-dark
{
  background-color: rgba(15, 15, 15, 0.8);
}

.solidback-light
{
  background-color: white;
}
.solidback-dark
{
  background-color: rgb(15, 15, 15);
}
.isolidback-light
{
  background-color: rgb(15, 15, 15);
}
.isolidback-dark
{
  background-color: white;
}

.alphaback-light
{
  background-color: rgba(15, 15, 15, 0.1);
}
.alphaback-dark
{
  background-color: rgba(255, 255, 255, 0.3);
}

.contrast-light
{
  background-color: rgb(247, 247, 247);
}
.contrast-dark
{
  background-color: rgb(30, 30, 30);
}

.text-light
{
  color: black;
}
.text-dark
{
  color: white;
}

.gtext-light
{
  color: gray;
}
.gtext-dark
{
  color: rgb(179, 179, 179);
}

.border-light
{
  border-color: black;
}
.border-dark
{
  border-color: white;
}

.boxshadow-light
{
  box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.5);
}
.boxshadow-dark
{
  box-shadow: 0 0 7px 0 rgb(255, 255, 255);
}

.image-dark
{
  filter: invert(100%);
}
.image-light
{
  filter: invert(0%);
}