/* Initial, variables settings, imports */

@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400&display=swap');

*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Comfortaa", Arial;
  scroll-behavior: smooth;
}

html, body, .App, #root
{
  width: 100%;
  min-height: 100%;
  height: fit-content;
}

a, input, textarea, ul, li, button, img, abbr
{
  text-decoration: none;
  list-style: none;
  outline: none;
  -webkit-user-drag: none;
  user-select: none;
}

::-webkit-scrollbar
{
  display: none;
}

::selection
{
  background-color: rgba(145, 0, 145, 0.7);
}

/*******************************/

.loading-container
{
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  background-color: transparent;
  width: 100%;
  height: 100%;
  z-index: 5;
  overflow: hidden;
}
.loading-door
{
  width: 50%;
  height: 100%;
  position: absolute;
}
.loading-door:first-of-type
{
  left: 0;
  border-right: 10px solid rgb(90, 0, 90);
}
.loading-door:last-of-type
{
  right: 0;
  border-left: 10px solid rgb(90, 0, 90);
}
.loading-loader
{
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  gap: 10px;
  top: 50%;
  transform: translate(0%, -50%);
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  z-index: 6;
  width: 100%;
}
.loading-cube
{
  margin: auto;
  width: 5vw;
  height: 5vw;
  min-width: 35px;
  min-height: 35px;
  background-color: rgb(184, 0, 184);
  border-radius: 5px;
  -moz-animation: cubespin 1s ease infinite;
  -webkit-animation: cubespin 1s ease infinite;
  -o-animation: cubespin 1s ease infinite;
  animation: cubespin 1s ease infinite;
  margin-bottom: 10vh;
}
.loading-title, .loading-text
{
  text-align: center;
}
.loading-title
{
  color: rgb(184, 0, 184);
  font-size: 30px;
  font-weight: normal;
  -moz-animation: opacity 1.25s ease-in-out;
  -webkit-animation: opacity 1.25s ease-in-out;
  -o-animation: opacity 1.25s ease-in-out;
  animation: opacity 1.25s ease-in-out;
}
.loading-text
{
  font-size: 20px;
  -o-animation: opacity 2s ease-in-out;
  -moz-animation: opacity 2s ease-in-out;
  -webkit-animation: opacity 2s ease-in-out;
  animation: opacity 2s ease-in-out;
}

.header
{
  width: 100%;
  height: 10vh;
  min-height: fit-content;
  position: fixed;
  top: 0;
  left: 0;
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 0 10px 0 rgba(15, 15, 15, 0.5);
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  z-index: 2;
}
.home-link
{
  color: rgb(184, 7, 184);
  font-size: 30px;
  font-weight: lighter;
  letter-spacing: 2.5px;
  margin-left: 15px;
  margin-right: auto;
}
.header-nav
{
  width: fit-content;
  height: 100%;
  margin-right: 35px;
  margin-left: auto;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  animation: opacity 0.2s ease-in-out;
}
.hn-link-list
{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 12.5px;
}
.hn-link
{
  font-size: 17px;
  font-weight: normal;
  color: rgb(202, 0, 202);
  padding: 7.5px;
  border: 1px rgb(202, 0, 202) solid;
  border-radius: 10px;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5);
  -o-transition: 0.2s ease-in-out;
  -moz-transition: 0.2s ease-in-out;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
  text-align: center;
}
.hn-link:hover
{
  color: white;
  background: linear-gradient(60deg, rgb(202, 10, 202), rgb(104, 0, 104));
}

.resp-nav-btn
{
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 7.5px 5px;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5);
}
.resp-nav-btn-line
{
  width: 25px;
  height: 2px;
  margin: auto;
}
.line-open
{
  transition: 0.5s ease-in-out;
}
.line-open:first-of-type
{
  position: absolute;
  transform: rotate(50deg);
  margin-top: 9px;
}
.line-open:last-of-type
{
  position: absolute;
  transform: rotate(-50deg);
  margin-top: 9px;
}
.line-open:not(:first-of-type):not(:last-of-type)
{
  transition: 0s;
  opacity: 0;
}

.sett-btn
{
  cursor: pointer;
  padding: 5px;
  border-radius: 50%;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5);
  border: none;
  margin-right: 15px;
  width: 35px;
  height: 35px;
}
.sett-btn img
{
  width: 100%;
  height: 100%;
}

.settings-container
{
  position: fixed;
  z-index: 1;
  width: 100%;
  height: 90vh;
  left: 0;
  top: 10vh;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(2.5px);
  -o-animation: opacity 0.3s ease-in;
  -moz-animation: opacity 0.3s ease-in;
  -webkit-animation: opacity 0.3s ease-in;
  animation: opacity 0.3s ease-in;
}
.settings-box
{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 70%;
  min-width: fit-content;
  height: fit-content;
  padding: 10px 15px;
  border-radius: 15px;
  margin-top: 5%;
  margin-bottom: auto;
}
.settings-topbar
{
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: fit-content;
  border-bottom: gray 1px solid;
  padding: 5px 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}
.settings-title
{
  font-weight: lighter;
  font-size: 20px;
  margin-left: 10px;
  margin-right: auto;
}
.settings-closebtn
{
  font-size: 20px;
  padding: 2.5px;
  padding-bottom: 0;
  margin-right: 10px;
  margin-left: auto;
  width: 30px;
  height: 30px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: 0.2s ease-in-out;
  border-radius: 4px;
}
.settings-closebtn:hover
{
  background-color: rgb(202, 0, 0);
  color: white;
}

.content
{
  width: 100%;
  height: fit-content;
  margin-top: 10vh;
}

.info-circle
{
  font-size: 15px;
  border-radius: 50%;
  border: gray 1px solid;
  color: gray;
  padding: 2px 7.5px;
  font-weight: bolder;
}

.scrolltop-btn
{
  width: 40px;
  height: 40px;
  border-radius: 15px;
  background-color: transparent;
  position: fixed;
  left: 20px;
  bottom: 20px;
  cursor: pointer;
  border: none;
  z-index: 2;
  -moz-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}
.scrolltop-btn:hover
{
  bottom: 25px;
}
.scrolltop-btn img
{
  width: 100%;
  height: 100%;
}

.footer
{
  width: 100%;
  height: fit-content;
  min-height: 10vh;
  position: relative;
  box-shadow: 0 0 10px 0 rgba(15, 15, 15, 0.5);
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.madewith
{
  width: fit-content;
  height: fit-content;
  padding: 15px 0;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5px;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}
.madewith-txt
{
  color: gray;
  font-size: 16px;
  font-weight: lighter;
}
.madewith .logos
{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}
.madewith .logos img
{
  height: 25px;
  width: auto;
}

.devby
{
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  width: fit-content;
  height: fit-content;
  margin: auto auto 0 auto;
  padding: 5px 0;
}
.devby p
{
  color: gray;
  font-size: 16px;
  display: inline-block;
}
.devby a
{
  color: rgb(184, 7, 184);
  font-size: 16px;
}

@media screen and (min-width: 0px) and (max-width: 550px)
{
  .header-nav
  {
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 10vh;
    display: none;
    align-items: center;
    justify-content: center;
    background-color: rgba(15, 15, 15, 0.8);
  }
  .hn-link-list
  {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 35px;
    width: 100%;
    height: 100%;
  }
  .hn-link-list li
  {
    width: 50%;
    min-width: fit-content;
    display: flex;
  }
  .hn-link
  {
    width: 100%;
    min-width: 200px;
  }
  .sett-btn
  {
    margin-left: 15px;
  }
  .resp-nav-btn
  {
    display: flex;
  }
  .scrolltop-btn
  {
    width: 25px;
    height: 25px;
    bottom: 30px;
    left: 10px;
  }
  .scrolltop-btn:hover
  {
    bottom: 35px;
  }
  .settings-box
  {
    width: 100%;
  }
  .loading-title
  {
    font-size: 22.5px;
  }
  .loading-text
  {
    font-size: 16px;
  }
}

@keyframes opacity
{
  from
  {
    opacity: 0.1;
  }
  to
  {
    opacity: 1;
  }
}

@keyframes cubespin
{
  from
  {
    transform: rotate(-45deg);
  }
  to
  {
    transform: rotate(45deg);
  }
}