.technologies-container
{
  position: fixed;
  z-index: 1;
  width: 100%;
  height: 90vh;
  left: 0;
  top: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(2.5px);
  animation: opacity 0.3s ease-in;
  -o-animation: opacity 0.3s ease-in;
  -moz-animation: opacity 0.3s ease-in;
  -webkit-animation: opacity 0.3s ease-in;
  cursor: default;
}
.technologies-box
{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 70%;
  min-width: 275px;
  height: 70vh;
  padding: 10px 15px;
  border-radius: 15px;
  margin: auto;
}
.technologies-topbar
{
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: fit-content;
  border-bottom: gray 1px solid;
  padding: 5px 0;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
}
.technologies-title
{
  font-weight: lighter;
  font-size: 20px;
  margin-left: 10px;
  margin-right: auto;
}
.technologies-closebtn
{
  font-size: 20px;
  padding: 2.5px;
  padding-bottom: 0;
  margin-right: 10px;
  margin-left: auto;
  width: 30px;
  height: 30px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: 0.2s ease-in-out;
  border-radius: 4px;
}
.technologies-closebtn:hover
{
  background-color: rgb(202, 0, 0);
  color: white;
}

.technologies-content
{
  width: 90%;
  height: 70%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 15px 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  margin: auto;
}

.technologies-text
{
  font-size: 18px;
  margin: 12.5px auto;
  text-align: center;
}

.slide-button
{
  height: fit-content;
  width: fit-content;
  padding: 30px 10px;
  cursor: pointer;
  border: none;
  font-size: 30px;
  text-align: center;
  border-radius: 5px;
  transition: 0.2s ease-in-out;
}
.slide-button:hover
{
  background-color: rgba(184, 0, 184, 0.8);
}


@media screen and (min-width: 0) and (max-width: 550px)
{
    .technologies-box
    {
      width: 100%;
    }
    .technologies-content
    {
      width: 100%;
    }
}