.certificate-card
{
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    width: 70%;
    height: 75px;
    gap: 10px;
}

.certificate-card-icon
{
    width: 60px;
    height: auto;
    margin: auto auto auto 10px;
}

.certificate-title
{
    font-size: 20px;
    font-weight: bold;
    margin-left: 0;
    margin-right: auto;
    text-align: center;
}

@media screen and (min-width: 0) and (max-width: 800px)
{
    .certificate-title
    {
        font-size: 16px;
    }
    .certificate-card
    {
        width: 90%;
    }
}
@media screen and (min-width: 0) and (max-width: 500px)
{
    .certificate-title
    {
        font-size: 15px;
    }
    .certificate-card-icon
    {
        width: 40px;
    }
}