.section
{
    padding: 5vw 0;
    width: 100%;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.section:nth-of-type(odd)
{
    background-color: rgb(114, 2, 114);
    box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.8);
}
.section::after
{
    content: '';
    width: 50%;
    height: 1px;
    border-radius: 50vw;
    position: relative;
    background-color: gray;
    margin: 20px auto;
}

.welcome-section
{
    width: 100%;
    min-height: 90vh;
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    margin-bottom: 10px;
    clip-path: polygon(0% 0%, 100% 0%, 100% 85%, 50% 100%, 0% 85%);
}
.welcome-container
{
    display: inline-flex;
    width: 100%;
    height: fit-content;
    height: 100%;
    padding-bottom: 100px;
    padding-top: 30px;
}

.welcome-art-container
{
    height: 100%;
    margin-right: auto;
    margin-left: 0;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    z-index: 1;
}
.tube
{
    background: linear-gradient(to right, rgb(114, 2, 114), rgb(228, 17, 228));
    border-top-right-radius: 50vw;
    border-bottom-right-radius: 50vw;
    position: relative;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.6);
    z-index: 1;
}
.art-separator
{
    background: linear-gradient(to right, rgb(114, 2, 114), rgb(228, 17, 228));
    width: 50%;
    height: 5px;
    border-radius: 12px;
    margin: 20px auto;
}
.welcome-text-container
{
    height: 100%;
    margin-left: auto;
    margin-right: 7.5%;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    z-index: 1;
}
.welcome-title
{
    font-size: 30px;
    padding-bottom: 10px;
    font-weight: lighter;
}
.welcome-text
{
    max-width: 350px;
    text-align: justify;
    font-size: 18px;
    margin: 0 auto;
}

.cube-animation-container
{
    position: absolute;
    width: 100%;
    height: 100vh;
    background-color: transparent;
    overflow: hidden;
}
.cubes
{
    position: absolute;
    background-color: rgba(187, 0, 187, 0.5);
    border-radius: 4px;
}

.section-title
{
    font-size: 35px;
    font-weight: lighter;
    text-align: center;
    padding: 15px 0;
}
.section:nth-of-type(odd) .section-title
{
    color: white;
}
.section-text
{
    max-width: 80%;
    text-align: justify;
    margin: auto;
    -webkit-user-select: text;
    -moz-user-select: text;
    user-select: text;
    font-size: 18px;
}
.section:nth-of-type(odd) .section-text
{
    color: rgb(185, 185, 185);
}
.section-card-container
{
    width: 70%;
    height: fit-content;
    padding: 10px 5px;
    margin: 15px auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 25px;
}

.project-filter
{
    width: fit-content;
    margin: auto;
    background-color: transparent;
    padding: 15px 0;
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.project-filter-button
{
    width: fit-content;
    height: fit-content;
    cursor: pointer;
    background-color: rgb(131, 0, 131);
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.3);
    border: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 2.5px;
    padding: 5px 7.5px;
    font-size: 14px;
    color: rgb(184, 184, 184);
    border-radius: 10px;
    transition: 0.15s ease-in-out;
}
.project-filter-button:hover
{
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.5);
}
.project-filter-button img
{
    width: 20px;
    height: 20px;
}

.project-filter-box
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;
    width: 100%;
    height: fit-content;
    padding: 12.5px 0;
    transform-origin: top;
}
.project-filter-box::before, .project-filter-box::after
{
    content: '';
    background-color: gray;
    width: 30%;
    height: 1px;
    position: relative;
    margin: 5px auto;
}
.project-filters
{
    border: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 10px 0;
}
.project-filter-apply-btn, .project-filter-clear-btn
{
    color: white;
    background-color: gray;
    border: none;
    cursor: not-allowed;
    padding: 5px 7.5px;
    border-radius: 6px;
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.3);
}

.filter 
{
    position: relative;
    display: block;
    padding-left: 23px;
    margin-bottom: 12px;
    font-size: 16px;
    color: white;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.filter .filter-checkbox
{
    position: absolute;
    height: 0;
    width: 0;
    cursor: pointer;
    opacity: 0;
}
.check
{
    position: absolute;
    width: 17px;
    height: 17px;
    left: 0;
    top: 0;
    border-radius: 4px;
    background-color: white;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.3);
}
.filter .filter-checkbox:checked ~ .check
{
    background-color: rgb(192, 0, 160);
}
.check:after
{
    content: '';
    display: none;
    position: absolute;
}
.filter input:checked ~ .check:after 
{
    display: block;
}
.filter .check:after
{
    height: 7px;
    width: 3px;
    top: 2px;
    left: 6px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.filters-btn-container
{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

@media screen and (min-width: 0) and (max-width: 1000px)
{
    .welcome-art-container
    {
        display: none;
    }
    .welcome-text-container
    {
        margin: auto;
    }
    .welcome-title
    {
        text-align: center;
    }
}
@media screen and (min-width: 0) and (max-width: 600px)
{
    .welcome-title
    {
        font-size: 23px;
    }
    .welcome-text
    {
        max-width: 90%;
    }
}
@media screen and (min-width: 0) and (max-width: 400px)
{
    .section-text
    {
        max-width: 95%;
        font-size: 16px;
    }
}