.project-card
{
    width: 175px;
    height: 175px;
    border-radius: 15px;
    box-shadow: 0 0 8px 1px rgb(0, 0, 0);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    user-select: none;
    background-color: white;
}
.project-card-img
{
    width: autos;
    height: 45%;
}
.project-card-title
{
    font-size: 17px;
    font-weight: lighter;
    text-align: center;
    margin-top: 5%;
}
.project-card-clicktxt
{
    font-size: 14px;
    font-weight: lighter;
    color: gray;
    text-align: center;
    margin-top: 5%;
    margin-bottom: 0;
    transform-origin: center;
    transition: 0.15s transform ease-in-out;
    transform: scale(0);
}
.project-year-version
{
    font-size: 13px;
    color: gray;
    margin-bottom: 0;
    margin-top: 5px;
    transform-origin: center;
    transition: 0.15s transform ease-in-out;
    transform: scale(0);
}

.project-card:hover > .project-card-clicktxt
{
    transform: scale(1);
}