.technology-card
{
  width: 70%;
  height: 100%;
  min-width: 275px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10%;
  border-radius: 5px;
}
.technology-art
{
    width: 20%;
    height: 20%;
    min-width: fit-content;
    min-height: fit-content;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 25px;
    clip-path: polygon(50% 0, 100% 50%, 50% 100%, 0 50%);
}
.technology-abbr
{
    font-size: 25px;
    font-weight: bold;
    color: black;
}
.technology-title
{
    font-weight: normal;
    font-size: 20px;
    text-align: center;
}
.technology-info
{
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 70%;
    min-width: 275px;
}
.technology-description
{
    font-size: 18px;
    text-align: justify;
    max-width: 100%;
}