.project-preview-container
{
  position: fixed;
  z-index: 1;
  width: 100%;
  height: 90vh;
  left: 0;
  top: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(2.5px);
  animation: opacity 0.3s ease-in;
  -o-animation: opacity 0.3s ease-in;
  -moz-animation: opacity 0.3s ease-in;
  -webkit-animation: opacity 0.3s ease-in;
  cursor: default;
}
.project-preview-box
{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 70%;
  min-width: 275px;
  height: 70vh;
  padding: 10px 15px;
  border-radius: 15px;
  margin: auto;
}
.project-preview-topbar
{
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: fit-content;
  border-bottom: gray 1px solid;
  padding: 5px 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}
.project-preview-title
{
  font-weight: lighter;
  font-size: 20px;
  margin-left: 10px;
  margin-right: auto;
}
.project-preview-closebtn
{
  font-size: 20px;
  padding: 2.5px;
  padding-bottom: 0;
  margin-right: 10px;
  margin-left: auto;
  width: 30px;
  height: 30px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  -moz-transition: 0.2s ease-in-out;
  -webkit-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}
.project-preview-closebtn:hover
{
  background-color: rgb(202, 0, 0);
  color: white;
}

.project-preview-content
{
    width: 90%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    padding: 15px 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    overflow-y: scroll;
}

.project-preview-icon
{
  width: 20%;
  height: auto;
  margin: auto;
  padding: 10px 0;
}

.project-preview-btn-container, .project-preview-info-box
{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 10%;
    width: 100%;
    padding: 15px 0;
}
.project-preview-desc-container
{
  display: flex;
  flex-direction: column;
  width: 70%;
  gap: 15px;
  padding: 10px 0;
}
.project-preview-desc-container::before
{
  content: '';
  width: 50%;
  background-color: gray;
  height: 1px;
  margin: auto;
}
.project-preview-btn
{
    border: 2px solid rgb(202, 10, 202);
    color: rgb(202, 10, 202);
    font-size: 18px;
    font-weight: normal;
    padding: 5px 10px;
    border-radius: 20px;
}
.project-preview-desc
{
    width: 100%;
    height: fit-content;
    font-size: 16px;
    text-align: justify;
}