.section-card
{
    width: 175px;
    height: 175px;
    border-radius: 15px;
    box-shadow: 0 0 8px 1px rgb(184, 7, 184);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    user-select: none;
}
.section-card:hover
{
    box-shadow: 0 0 7px 2px rgb(184, 7, 184);
}
.section-card:hover > img
{
    height: 70%;
}
.section-card img
{
    width: auto;
    height: 60%;
    margin: 2% auto 0 auto;
    -webkit-user-drag: none;
    transition: 0.2s height ease-in-out;
}
.section-card-title
{
    font-size: 16px;
    font-weight: normal;
    margin: auto;
    max-width: 95%;
    color: rgb(184, 7, 184);
}