.banner
{
    width: 90%;
    height: fit-content;
    position: relative;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.5);
    margin: 10vh auto 0 auto;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    border-radius: 10px;
}

.banner-content
{
    height: 20vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    gap: 5px;
    border-radius: 10px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.banner-title
{
    font-size: 27px;
    color: white;
    margin-left: 10px;
}

.banner-subtitle
{
    margin-left: 10px;
    color: rgb(161, 161, 161);
}

@media screen and (min-width: 0) and (max-width: 600px)
{
    .banner-content
    {
        height: 15vh;
    }
    .banner-title
    {
        font-size: 20px;
        margin: auto;
    }
    .banner-subtitle
    {
        margin: auto;
    }
}
@media screen and (min-width: 0) and (max-width: 400px)
{
    .banner
    {
        width: 95%;
    }
    .banner-title
    {
        font-size: 18px;
        margin: auto;
    }
}