.settings-content
{
    width: 90%;
    height: fit-content;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 15px 0;
    user-select: none;
    -webkit-user-select: none;
    gap: 20px;
}
.settings-group
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: fit-content;
    gap: 5px;
}
.settings-group-title
{
    font-size: 20px;
    font-weight: normal;
    padding: 15px 0;
    width: 50%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;
    letter-spacing: 2px;
}
.settings-group-title::after
{
    content: '';
    background-color: gray;
    width: 100%;
    height: 1px;
}
.settings-item
{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}
.settings-item-title
{
    font-size: 18px;
}

.switch
{
    height: 28.5px;
    width: 50px;
    display: inline-block;
    position: relative;
}
.switch input
{
    width: 0;
    height: 0;
    opacity: 0;
}

.toggle
{
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: crimson;
    cursor: pointer;
    border-radius: 28.5px;
    -webkit-transition: 0.3s ease-in-out;
    -o-transition: 0.3s ease-in-out;
    -moz-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
}
.toggle:before
{
    position: absolute;
    content: '';
    width: 21px;
    height: 21px;
    bottom: 4px;
    left: 4px;
    border-radius: 50%;
    background-color: white;
    -webkit-transition: 0.3s ease-in-out;
    -o-transition: 0.3s ease-in-out;
    -moz-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
}
.switch input:checked + .toggle
{
    background-color: green;
}

.switch input:focus + .toggle
{
    box-shadow: 0 0 1px green;
}

.switch input:checked + .toggle:before
{
    -ms-transform: translateX(21px);
    -webkit-transform: translateX(21px);
    -o-transform: translateX(21px);
    transform: translateX(21px);
}

.dropdown
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.dropdown-box
{
    border-radius: 10px;
    padding: 5px 10px;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    gap: 10px;
}
.dropdown-selected
{
    font-size: 16px;
}
.dropdown-arrow
{
    width: 12.5px;
    height: 12.5px;
    margin: auto;
    border: 4px solid;
    transform: rotate(45deg);
}
.dropdown-list
{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 105px;
    width: fit-content;
    border-radius: 6px;
    overflow: hidden;
}
.option
{
    padding: 7.5px 5px;
    transition: 0.2s ease-in-out;
    width: 100%;
    cursor: pointer;
    text-align: center;
}
.option:hover
{
    background-color: rgba(110, 110, 110, 0.3);
}

@media screen and (min-width: 0) and (max-width: 600px)
{
    .settings-group-title
    {
        font-size: 18px;
    }
    .settings-item-title
    {
        font-size: 16px;
    }
}
@media screen and (min-width: 0) and (max-width: 400px)
{
    .settings-item-title
    {
        font-size: 14px;
    }
}